nav {
	width: 100%;
	position: sticky;
	z-index: 10;
	top: 0;
	padding: 1.2em 0;
	color: white;
	background-color: rgb(134, 54, 85);
}

.appName {
	font-size: 50px;
	font-weight: 1000;
	margin: 0 0 0 50px;
}

.fas {
	margin: 0 50px 0 10px;
}

nav span {
	font-size: 18px;
	border-bottom: 1px solid transparent;
	transition: border-bottom 0.2s ease;
	transition-delay: 0.25s;
	margin: 1em;
	padding-bottom: 0.3em;
}

nav span:hover {
	cursor: pointer;
	border-bottom: 1px solid white;
}

nav span:hover i {
	color: gold;
}

nav span.activeClass i {
	color: gold;
}

nav span.activeClass {
	border-bottom-color: rgb(255, 230, 0);
}
