@import url(https://fonts.googleapis.com/css2?family=Libre+Franklin:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400&display=swap);
:root {
	--darkAccent: rgba(2, 0, 36, 1);
	--lightAccent: #f3d6e2;
}

* {
	box-sizing: border-box;
}

*,
html {
	line-height: 1em;
	padding: 0;
	margin: 0;
	font-family: 'Libre Franklin', sans-serif;
}

body {
	margin: 0;
	padding: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
		'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}

h3 {
	font-size: 2.6em;
	text-align: center;
	padding: 1em;
	font-weight: 500;
}

nav {
	width: 100%;
	position: -webkit-sticky;
	position: sticky;
	z-index: 10;
	top: 0;
	padding: 1.2em 0;
	color: white;
	background-color: rgb(134, 54, 85);
}

.appName {
	font-size: 50px;
	font-weight: 1000;
	margin: 0 0 0 50px;
}

.fas {
	margin: 0 50px 0 10px;
}

nav span {
	font-size: 18px;
	border-bottom: 1px solid transparent;
	transition: border-bottom 0.2s ease;
	transition-delay: 0.25s;
	margin: 1em;
	padding-bottom: 0.3em;
}

nav span:hover {
	cursor: pointer;
	border-bottom: 1px solid white;
}

nav span:hover i {
	color: gold;
}

nav span.activeClass i {
	color: gold;
}

nav span.activeClass {
	border-bottom-color: rgb(255, 230, 0);
}

section {
	display: flex;
	align-items: center;
	justify-content: center;
	height: 100vh;
	width: 100%;
	border-bottom: 1px solid lightgrey;
}

section:nth-child(even) {
	flex-direction: row-reverse;
}

section img,
section div {
	height: auto;
	width: 100%;
}

section img {
	width: 35%;
	margin: 0 50px 0 50px;
}

.aboutSeniorIcon {
	width: 75%;
	height: 75%;
}

.taskSeniorIcon {
	width: 40%;
	height: 40%;
}

section div {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: flex-start;
	padding: 1em;
}

.homeIntro, .aboutParagraph, .referenceParagraph {
	font-size: 20px;
	line-height: 40px;
	margin: 0 50px 0 50px;
}

@media screen and (max-width: 900px) {
	section,
	section:nth-child(even) {
		flex-direction: column;
	}

	section img {
		height: auto;
		width: auto;
		max-width: 100vw;
	}
}

/* Tasks of the day styling */
.todoList {
	background: #e2c5d9;
	margin: 0 50px 0 0;
	border-radius: 20px;
	padding: 2px;
	max-width: 800px;
}

.todo-container {
	background: #e5d1e3;
	border-radius: 20px;
	padding: 20px;
	max-width: 400px;
	font-size: 25px;
	font-weight: 600;
}

.todo-container input {
	border: 0;
	width: 200px;
	height: 50px;
	padding: 0 20px;
	margin: 20px;
	font-size: 20px;
	border-radius: 10px;
	color: rgb(100, 35, 86);
}

.todo-container input:focus, #to-do-form button:focus {
	outline: none;
}

button {
	height: 50px;
	width: 150px;
	border-radius: 5px;
	border: 2px #a11344 solid;
	background: #f8f7c0;
	cursor: pointer;
	font-weight: bolder;
	font-size: 20px;
}

/* Memory Styling */
.memory-section {
	position: top;
	display: flex;
	align-self: top;
	justify-content: flex-start;
}
.memory-container {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	padding: 1em;
}
.upload-widget {
	height: 10vh;
	width: 100%;
	padding: 1em;
}

